"use client";

import Head from "next/head";
import Image from "next/image";
import { useState, useRef, useEffect } from "react";
import html2canvas from "html2canvas";
import { jsPDF } from "jspdf";

export default function VaccineForm() {
  const [formData, setFormData] = useState({
    childName: "",
    dateOfBirth: "",
    placeOfBirth: "",
    address: "",
    motherName: "",
    fatherName: "",
    birthHeight: "",
    birthWeight: "",
    // This will hold either "male" or "female"
    sex: "",
    healthCenter: "",
    barangay: "",
    familyNo: "",
  });

  // Create a ref to target the entire form container
  const formRef = useRef<HTMLDivElement>(null);

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const vaccineTypeCode_1 = {
    updateFirstDose: "Dose 1",
  };
  const vaccineTypeCode_2 = {
    updateFirstDose: "Dose 1",
  };
  const vaccineTypeCode_3 = {
    updateFirstDose: "Dose 1",
    updateSecondDose: "Dose 2",
    updateThirdDose: "Dose 3",
  };
  const vaccineTypeCode_4 = {
    updateFirstDose: "Dose 1",
    updateSecondDose: "Dose 2",
    updateThirdDose: "Dose 3",
  };
  const vaccineTypeCode_5 = {
    updateFirstDose: "Dose 1",
    updateSecondDose: "Dose 2",
  };
  const vaccineTypeCode_6 = {
    updateFirstDose: "Dose 1",
    updateSecondDose: "Dose 2",
    updateThirdDose: "Dose 3",
  };
  const vaccineTypeCode_7 = {
    updateFirstDose: "Dose 1",
    updateSecondDose: "Dose 2",
  };

  // Function to capture the form and generate the PDF
  const handleSavePDF = async () => {
    if (!formRef.current) return;

    // Use html2canvas to take a screenshot of the form
    const canvas = await html2canvas(formRef.current, { scale: 2 });

    // Convert the canvas to an image data URL
    const imgData = canvas.toDataURL("image/png");

    // Create a new jsPDF instance with dimensions matching the canvas
    const pdf = new jsPDF({
      orientation: "landscape",
      unit: "px",
      format: [canvas.width, canvas.height],
    });

    // Add the image to the PDF at (0, 0)
    pdf.addImage(imgData, "PNG", 0, 0, canvas.width, canvas.height);

    // Save the generated PDF with the desired filename
    pdf.save("vaccine_form.pdf");
  };

  // Prevent zooming via mouse wheel (ctrl+wheel) on desktop browsers
  useEffect(() => {
    const handleWheel = (e: WheelEvent) => {
      if (e.ctrlKey) {
        e.preventDefault();
      }
    };
    window.addEventListener("wheel", handleWheel, { passive: false });
    return () => window.removeEventListener("wheel", handleWheel);
  }, []);

  // Prevent pinch zoom (gesture events) on mobile browsers (e.g., Safari)
  useEffect(() => {
    const preventGesture = (e: Event) => {
      e.preventDefault();
    };
    document.addEventListener("gesturestart", preventGesture);
    document.addEventListener("gesturechange", preventGesture);
    document.addEventListener("gestureend", preventGesture);
    return () => {
      document.removeEventListener("gesturestart", preventGesture);
      document.removeEventListener("gesturechange", preventGesture);
      document.removeEventListener("gestureend", preventGesture);
    };
  }, []);

  return (
    <>
      {/* Meta tag to restrict viewport scaling */}
      <Head>
        <meta
          name="viewport"
          content="width=device-width, initial-scale=1, maximum-scale=1, user-scalable=no"
        />
      </Head>
      <div>
        {/* Container for the form image and static text elements */}
        <div
          className="relative w-4/5 mx-auto"
          style={{ height: "80vh" }}
          ref={formRef}
        >
          <Image
            src="/vaccine form.jpg"
            alt="Vaccine Form"
            fill
            // This style ensures the image maintains its aspect ratio without stretching
            style={{ objectFit: "contain" }}
            className="object-contain"
          />
          {/* Text Overlay */}
          <div className="absolute top-9 left-10 w-full">
            <span className="absolute top-[47px] left-[270px] w-[200px] bg-transparent">
              {formData.childName || "Child's Name"}
            </span>
            <span className="absolute top-[70px] left-[270px] w-[200px] bg-transparent">
              {formData.dateOfBirth || "Date of Birth"}
            </span>
            <span className="absolute top-[97px] left-[270px] w-[200px] bg-transparent">
              {formData.placeOfBirth || "Place of Birth"}
            </span>
            <span className="absolute top-[123px] left-[270px] w-[250px] bg-transparent">
              {formData.address || "Address"}
            </span>
            <span className="absolute top-[47px] left-[700px] w-[200px] bg-transparent">
              {formData.motherName || "Mother's Name"}
            </span>
            <span className="absolute top-[70px] left-[700px] w-[200px] bg-transparent">
              {formData.fatherName || "Father's Name"}
            </span>
            <span className="absolute top-[94px] left-[690px] w-[200px] bg-transparent">
              {formData.birthHeight || "Birth Height"}
            </span>
            <span className="absolute top-[117px] left-[690px] w-[200px] bg-transparent">
              {formData.birthWeight || "Birth Weight"}
            </span>

            {/* Gender Selection Dots in a Column */}
            <div className="absolute top-[153px] left-[570px] flex flex-col items-center gap-2">
              {/* Male Dot */}
              <div
                onClick={() => setFormData({ ...formData, sex: "male" })}
                className={`w-3.5 h-3.5 rounded-full border border-gray-400 cursor-pointer ${
                  formData.sex === "male" ? "bg-blue-500" : "bg-transparent"
                }`}
                title="Male"
              ></div>
              {/* Female Dot */}
              <div
                onClick={() => setFormData({ ...formData, sex: "female" })}
                className={`w-3.5 h-3.5 rounded-full border border-gray-400 cursor-pointer ${
                  formData.sex === "female" ? "bg-pink-500" : "bg-transparent"
                }`}
                title="Female"
              ></div>
            </div>

            <span className="absolute top-[47px] left-[1200px] w-[200px] bg-transparent">
              {formData.healthCenter || "Health Center"}
            </span>
            <span className="absolute top-[70px] left-[1200px] w-[200px] bg-transparent">
              {formData.barangay || "Baranggay"}
            </span>
            <span className="absolute top-[95px] left-[1180px] w-[200px] bg-transparent">
              {formData.familyNo || "Family Number"}
            </span>
            {/* vaccine 1 */}
            <span className="absolute top-[295px] left-[545px] w-[200px] bg-transparent">
              {vaccineTypeCode_1.updateFirstDose}
            </span>
            {/* vaccine 2 */}
            <span className="absolute top-[335px] left-[545px] w-[200px] bg-transparent">
              {vaccineTypeCode_2.updateFirstDose}
            </span>
            {/* vaccine 3 */}
            <span className="absolute top-[375px] left-[500px] w-[200px] bg-transparent">
              {vaccineTypeCode_3.updateFirstDose}
            </span>
            <span className="absolute top-[375px] left-[580px] w-[200px] bg-transparent">
              {vaccineTypeCode_3.updateSecondDose}
            </span>
            <span className="absolute top-[375px] left-[670px] w-[200px] bg-transparent">
              {vaccineTypeCode_3.updateThirdDose}
            </span>

            {/* vaccine 4 */}
            <span className="absolute top-[415px] left-[500px] w-[200px] bg-transparent">
              {vaccineTypeCode_4.updateFirstDose}
            </span>
            <span className="absolute top-[415px] left-[580px] w-[200px] bg-transparent">
              {vaccineTypeCode_4.updateSecondDose}
            </span>
            <span className="absolute top-[375px] left-[670px] w-[200px] bg-transparent">
              {vaccineTypeCode_4.updateThirdDose}
            </span>
          </div>
        </div>

        {/* Save to PDF Button */}
        <div className="text-center mt-4">
          <button
            onClick={handleSavePDF}
            className="px-4 py-2 bg-blue-500 text-white rounded hover:bg-blue-600"
          >
            Save to PDF
          </button>
        </div>
      </div>
    </>
  );
}
